.comments-form {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    bottom: 0;
    width: 100%;
}

.single-comment > img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
}

.single-comment {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 45%;
    margin: 10px;
}

.comments {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}

@media screen and (max-width: 480px) {
    .comments {
        width: 100%;
    }
    .single-comment {
        width: 100%;
        justify-content: space-between;
    }
}