.search-form {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0px;
}

.search-input {
    width: 30%;
    border: 1px solid #161616;
    margin: 0px;
    border-radius: 36px;
}

.search-btn {
    cursor: pointer;
}

.searched-user {
    display: flex;
    align-items: center;
}

.searched-user > img {
    border-radius: 50%;
    width: 40px;
    height: 40px;
}