@import url(https://fonts.googleapis.com/css2?family=Neucha&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Carattere&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Neucha', cursive;
}

.error {
  color: red;
}

.grid-container {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 39px;
}

.user-images {
  width: 350px;
  height: 300px;
}

.user-container {
  width: 60%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 20px;
}

@media  screen and (max-width: 480px) {
  
.user-container {
  width: 60%;
  display: grid;
  grid-template-columns: 1fr;
}
.title-insta {
  font-size: 20px;
}
.not-user > p {
  font-size:20px
}
}
.main-parent {
  display: flex;
  width: 50%;
  align-items: center;
  justify-content: center;
  margin-top: 40px;
}

button {
  background-color: #161616;
  color: white;
  padding: 5px 20px 5px 20px;
  border-radius: 36px;
  cursor: pointer;
  transition: 400ms;
}

button:hover {
  background-color: #e4e4e4;
  color: black;
  transition: 400ms;
}

.not-user {
  width: 100%;
  height: 94vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-image: url(/static/media/instagram.2ab30582.png);
}

.title-insta {
  color: white;
  
}

.title-logo {
  font-family: 'Carattere', cursive;
  font-size: 50px;
}

.slogan {
  color: aliceblue;
  font-size: 35px;
  font-style: italic;
}

.title-link {
  color: white;
  text-decoration: none;
  border-bottom: 1px solid black;
  font-size: 30px;
}
nav, .icons {
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.icons {
    width: 250px;
}

.nav-router {
    cursor: pointer;
    color: black;
}

.nav-image {
    width: 60px;
    height: 60px;
    border-radius: 50%;
}
.search-form {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0px;
}

.search-input {
    width: 30%;
    border: 1px solid #161616;
    margin: 0px;
    border-radius: 36px;
}

.search-btn {
    cursor: pointer;
}

.searched-user {
    display: flex;
    align-items: center;
}

.searched-user > img {
    border-radius: 50%;
    width: 40px;
    height: 40px;
}
form {
    max-width: 400px;
    margin: 0 auto;
    padding: 30px;
    border-radius: 8px;
    box-shadow: 1px 2px 3px rgba(50,50,50,0.05);
    border: 1px solid  var(--secondary);
    background: white;
  }
  input, textarea {
    border: 0;
    border-bottom: 1px solid var(--secondary);
    padding: 10px;
    outline: none;
    display: block;
    width: 100%;
    box-sizing: border-box;
    margin: 20px auto;
  }
.comments-form {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    bottom: 0;
    width: 100%;
}

.single-comment > img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
}

.single-comment {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 45%;
    margin: 10px;
}

.comments {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}

@media screen and (max-width: 480px) {
    .comments {
        width: 100%;
    }
    .single-comment {
        width: 100%;
        justify-content: space-between;
    }
}
